import {Link} from 'next'

interface SidebarProps {
  isVisible: boolean;
  isMobile: boolean;
  onBackDropClick: () => void;
}

interface navData {
  title: string;
  url:  string;
}
const data : navData[] | [] = [
  {
    title: "Dashboard",
    url: "/home/dashboard",
  },
  {
    title: "Transaction",
    url: "/home/transactions",
  },
  {
    title: "Reports",
    url: "/home/reports",
  },
  {
    title: "Settings",
    url: "/home/settings",
  },
];

export default function SideBar({
  isVisible,
  isMobile,
  onBackDropClick,
}: SidebarProps) {
  return (
    <div className="side-bar">
      {isMobile && isVisible && (
        <div
          className="fixed inset-0 bg-black bg-opacity-50 z-40 mt-16"
          onClick={onBackDropClick}
          aria-hidden="true"
        />
      )}

      {/* Sidebar */}
      <aside
        className={`
          ${isMobile ? "fixed" : "relative"} 
          ${isVisible ? "translate-x-0" : "-translate-x-full"}
          ${isMobile ? "z-50" : "z-auto"}
          ${!isMobile && !isVisible ? "w-0" : "w-64"}
          h-full bg-white transition-all px-[16px] sm:px-[48px] duration-300 overflow-hidden
        `}
        aria-hidden={!isVisible}
      >
        <div className="">
          {" "}
          <nav className="flex-1 space-y-1">
            {data.map((item : navData, idx : string) => {
              <Link key={idx} href={item.url}>
                {item.title}
              </Link>;
            })}
          </nav>
        </div>
      </aside>
    </div>
  );
}
